export function createTicketDefinition(
  httpClient: HttpClient
): CreateTicketDefinitionSignature {
  return (
    ticketDefinition: NonNullablePaths<
      TicketDefinition,
      `eventId` | `feeType` | `name` | `pricingMethod`,
      2
    >,
    options?: CreateTicketDefinitionOptions
  ) =>
    universalCreateTicketDefinition(
      ticketDefinition,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateTicketDefinitionSignature {
  /**
   * Creates a ticket definition.
   *
   * You may create up to 100 definitions per event.
   * @param - Ticket definition info.
   * @param - Currency info.
   * @returns Created ticket definition.
   */
  (
    ticketDefinition: NonNullablePaths<
      TicketDefinition,
      `eventId` | `feeType` | `name` | `pricingMethod`,
      2
    >,
    options?: CreateTicketDefinitionOptions
  ): Promise<
    NonNullablePaths<
      TicketDefinition,
      | `hidden`
      | `limited`
      | `pricingMethod.fixedPrice.value`
      | `pricingMethod.fixedPrice.currency`
      | `pricingMethod.pricingOptions.optionDetails`
      | `pricingMethod.pricingType`
      | `pricingMethod.free`
      | `feeType`
      | `salePeriod.displayNotOnSale`
      | `saleStatus`
      | `eventDetails.location.type`
      | `eventDetails.location.address.streetAddress.number`
      | `eventDetails.location.address.streetAddress.name`
      | `eventDetails.dateAndTimeSettings.recurrenceStatus`
      | `eventDetails.dateAndTimeSettings.recurringEvents.individualEventDates`
      | `eventDetails.dateAndTimeSettings.recurringEvents.individualEventDates.${number}.showTimeZone`
      | `eventDetails.status`
      | `seatingDetails.seats`
      | `seatingDetails.areas`,
      7
    > & {
      __applicationErrorsType?: CreateTicketDefinitionApplicationErrors;
    }
  >;
}

