/**
 * A Ticket Definition is a reusable configuration for an event that specifies ticket settings such as pricing, service fee handling, availability limits, and sale period.
 *
 * You can use Ticket Definitions to define and manage ticket types, control inventory and sale windows, and automate updates via the API.
 *
 * Read more about [Ticket Definitions](https://support.wix.com/en/article/creating-tickets-for-your-event).
 */
export interface TicketDefinition {
  /**
   * Ticket definition ID.
   * @format GUID
   * @readonly
   */
  _id?: string | null;
  /**
   * Event ID to which the ticket definition belongs.
   * @format GUID
   * @immutable
   */
  eventId?: string | null;
  /**
   * Revision number, which increments by 1 each time the ticket definition is updated. To prevent conflicting changes, the existing `revision` must be used when updating a ticket definition.
   * @readonly
   */
  revision?: string | null;
  /**
   * Date and time the ticket definition was created.
   * @readonly
   */
  _createdDate?: Date | null;
  /**
   * Date and time the ticket definition was updated.
   * @readonly
   */
  _updatedDate?: Date | null;
  /**
   * Ticket definition name.
   * @minLength 1
   * @maxLength 30
   */
  name?: string | null;
  /**
   * Ticket definition description.
   * @maxLength 500
   */
  description?: string | null;
  /**
   * Ticket definition policy.
   * @maxLength 1000
   */
  policyText?: string | null;
  /** Whether this ticket definition is hidden from site visitors and can't be purchased. */
  hidden?: boolean;
  /**
   * Whether the ticket has a limited maximum quantity.
   * @readonly
   */
  limited?: boolean;
  /** The maximum number of tickets that can be sold for the event when first defining the event. If a seating map is defined after you created a ticket definition, this property is ignored and `actualLimit` is used instead. */
  initialLimit?: number | null;
  /**
   * The maximum number of tickets that can be sold for the event after adding a seating map to the event. If no seating map is defined, this property is the same as `initialLimit`.
   * @readonly
   */
  actualLimit?: number | null;
  /** Ticket pricing method. */
  pricingMethod?: PricingMethod;
  /** Type of ticket service fee to collect. */
  feeType?: FeeTypeEnumTypeWithLiterals;
  /** Ticket sale period. */
  salePeriod?: SalePeriod;
  /**
   * Ticket sale status.
   * @readonly
   */
  saleStatus?: SaleStatusEnumStatusWithLiterals;
  /**
   * Ticket sales information. <br> <br>
   * **Note:** This field is only returned when `"SALES_DETAILS"` is specified in `field` in the request.
   */
  salesDetails?: SalesDetails;
  /**
   * Number of tickets that can be purchased per checkout. <br> <br>
   * **Note:** If the `actualLimit` or `salesDetails.unsoldCount` field value is smaller than `limitPerCheckout`, then it overrides this field.
   * @readonly
   * @max 50
   */
  limitPerCheckout?: number | null;
  /** Data extensions. */
  extendedFields?: ExtendedFields;
  /**
   * Information about the even the ticket is for. <br>
   * **Note:** This field is only returned when `"EVENT_DETAILS"` is specified in `field` in the request.
   */
  eventDetails?: EventDetails;
  /**
   * Seating information including available seats and areas for this ticket definition. <br>
   * **Note:** This field is only returned when `"SEATING_DETAILS"` is specified in `field` in the request and when the event has a seating plan.
   */
  seatingDetails?: SeatingDetails;
}

export interface SalePeriod {
  /** Date and time the ticket sale starts. */
  startDate?: Date | null;
  /** Date and time the ticket sale ends. */
  endDate?: Date | null;
  /** Whether to display the ticket if it's not available to buy. */
  displayNotOnSale?: boolean;
}

export interface PricingMethod extends PricingMethodPriceOneOf {
  /** Same ticket price for everyone. */
  fixedPrice?: CommonMoney;
  /** Guests choose how much they'd like to pay for the ticket. You can set the minimum price, or specify `"0"` in the request to make the ticket free. The price can be updated to a higher amount by a guest during the checkout. */
  guestPrice?: CommonMoney;
  /** Sets of various ticket prices. For example, you can charge different prices for children and adults. */
  pricingOptions?: PricingOptions;
  /**
   * Ticket price type.
   * @readonly
   */
  pricingType?: PricingTypeEnumTypeWithLiterals;
  /**
   * Whether the ticket is free. To create a free ticket, enter `"0"` in `pricingMethod.fixedPrice.value`.
   * @readonly
   */
  free?: boolean;
}

/** @oneof */
export interface PricingMethodPriceOneOf {
  /** Same ticket price for everyone. */
  fixedPrice?: CommonMoney;
  /** Guests choose how much they'd like to pay for the ticket. You can set the minimum price, or specify `"0"` in the request to make the ticket free. The price can be updated to a higher amount by a guest during the checkout. */
  guestPrice?: CommonMoney;
  /** Sets of various ticket prices. For example, you can charge different prices for children and adults. */
  pricingOptions?: PricingOptions;
}

/**
 * Money.
 * Default format to use. Sufficiently compliant with majority of standards: w3c, ISO 4217, ISO 20022, ISO 8583:2003.
 */
export interface CommonMoney {
  /**
   * Monetary amount. Decimal string with a period as a decimal separator (e.g., 3.99). Optionally, a single (-), to indicate that the amount is negative.
   * @format DECIMAL_VALUE
   */
  value?: string;
  /**
   * Currency code. Must be a valid [ISO 4217](https://www.iso.org/iso-4217-currency-codes.html) currency code (e.g., USD).
   * @format CURRENCY
   */
  currency?: string;
}

export interface PricingOptions {
  /**
   * Ticket price options.
   * @maxSize 100
   */
  optionDetails?: OptionDetails[];
}

export interface OptionDetails {
  /**
   * Ticket price option ID.
   * @format GUID
   */
  optionId?: string | null;
  /**
   * Ticket price option name, such as "Child Ticket".
   * @minLength 1
   * @maxLength 200
   */
  name?: string | null;
  /** Ticket price. */
  price?: CommonMoney;
}

export enum PricingTypeEnumType {
  /** Undefined pricing type. */
  UNKNOWN_PRICING_TYPE = 'UNKNOWN_PRICING_TYPE',
  /** All money goes to a seller. Applies to all ticket pricing methods except for `guestPrice`. */
  STANDARD = 'STANDARD',
  /** All collected money is a donation. This pricing type is automatically assigned when you select the `guestPrice` pricing method. */
  DONATION = 'DONATION',
}

/** @enumType */
export type PricingTypeEnumTypeWithLiterals =
  | PricingTypeEnumType
  | 'UNKNOWN_PRICING_TYPE'
  | 'STANDARD'
  | 'DONATION';

export enum FeeTypeEnumType {
  /** Unknown fee type. */
  UNKNOWN_FEE_TYPE = 'UNKNOWN_FEE_TYPE',
  /** The fee is deducted from the ticket price for a seller. <br><br> For example, if you're selling tickets for $10, then a ticket service fee of $0.25 will be deducted from the price and you'll get $9.75. */
  FEE_INCLUDED = 'FEE_INCLUDED',
  /** The fee is shown in addition to the ticket price at checkout and a guest pays the fee. <br><br> For example, if you sell tickets for $10, a customer will see a ticket service fee of $0.25 and will pay $10.25 in total. */
  FEE_ADDED_AT_CHECKOUT = 'FEE_ADDED_AT_CHECKOUT',
  /** Ticket service fee isn't collected. Available only for free tickets and legacy Wix users. */
  NO_FEE = 'NO_FEE',
}

/** @enumType */
export type FeeTypeEnumTypeWithLiterals =
  | FeeTypeEnumType
  | 'UNKNOWN_FEE_TYPE'
  | 'FEE_INCLUDED'
  | 'FEE_ADDED_AT_CHECKOUT'
  | 'NO_FEE';

export enum SaleStatusEnumStatus {
  /** Undefined sale status. */
  UNKNOWN_SALE_STATUS = 'UNKNOWN_SALE_STATUS',
  /** Tickets aren't on sale yet. */
  SALE_SCHEDULED = 'SALE_SCHEDULED',
  /** Tickets are on sale. */
  SALE_STARTED = 'SALE_STARTED',
  /** Tickets are no longer on sale. */
  SALE_ENDED = 'SALE_ENDED',
}

/** @enumType */
export type SaleStatusEnumStatusWithLiterals =
  | SaleStatusEnumStatus
  | 'UNKNOWN_SALE_STATUS'
  | 'SALE_SCHEDULED'
  | 'SALE_STARTED'
  | 'SALE_ENDED';

